import { defineStore } from 'pinia'

export const useBookingStore = defineStore('bookings', {
    state: () => ({
        bookings: [],
        rooms: ['A', 'B', 'C', 'D', 'E', 'F']
    }),
    actions: {
        fetchBookings() {
            const storedBookings = localStorage.getItem('bookings')
            if (storedBookings) {
                this.bookings = JSON.parse(storedBookings)
            } else {
                this.bookings = [
                    {
                        id: '1',
                        title: ' A - ',
                        start: '2025-06-15T10:00:00',
                        end: '2025-06-15T12:00:00',
                        room: 'A'
                    },
                    {
                        id: '2',
                        title: ' B - ',
                        start: '2025-06-15T14:00:00',
                        end: '2025-06-15T15:30:00',
                        room: 'B'
                    },
                    {
                        id: '3',
                        title: ' C - ',
                        start: '2025-06-16T09:00:00',
                        end: '2025-06-16T10:30:00',
                        room: 'C'
                    },
                    {
                        id: '4',
                        title: ' D - ',
                        start: '2025-06-16T13:00:00',
                        end: '2025-06-16T14:00:00',
                        room: 'D'
                    },
                    {
                        id: '5',
                        title: ' E - ',
                        start: '2025-06-17T11:00:00',
                        end: '2025-06-17T12:30:00',
                        room: 'E'
                    },
                    {
                        id: '6',
                        title: ' F - ',
                        start: '2025-06-17T15:00:00',
                        end: '2025-06-17T16:30:00',
                        room: 'F'
                    }
                ]
            }
            this.saveBookings()
        },
        addBooking(booking) {
            this.bookings.push({
                id: Date.now().toString(),
                ...booking
            })
            this.saveBookings()
        },
        updateBooking(id, updatedBooking) {
            const index = this.bookings.findIndex(b => b.id === id)
            if (index !== -1) {
                this.bookings[index] = { ...this.bookings[index], ...updatedBooking }
                this.saveBookings()
            }
        },
        deleteBooking(id) {
            this.bookings = this.bookings.filter(b => b.id !== id)
            this.saveBookings()
        },
        saveBookings() {
            localStorage.setItem('bookings', JSON.stringify(this.bookings))
            localStorage.setItem('rooms', JSON.stringify(this.rooms))
        }
    }
})