<template>
  <div class="container mx-auto p-4">
    <h1 class="text-2xl font-bold mb-4"></h1>
    
    <!--  -->
    <div class="mb-4 relative">
      <a href="#" class="room-info-link text-blue-600 hover:underline" @mouseover="showImage = true" @mouseleave="showImage = false">
        
      </a>
      <div v-if="showImage" class="room-image-preview">
        <img :src="`${process.env.BASE_URL}images/room_layout.jpg`" alt="" class="max-w-full max-h-80 object-contain" />
      </div>
    </div>

    <!--  -->
    <div class="mb-4">
      <label for="roomFilter" class="mr-2 text-sm font-medium text-gray-700"></label>
      <select
        id="roomFilter"
        v-model="roomFilter"
        class="p-2 border border-gray-300 rounded-md"
        @change="applyFilter"
      >
        <option value=""></option>
        <option v-for="room in bookingStore.rooms" :key="room" :value="room">
           {{ room }}
        </option>
      </select>
    </div>
    
    <!-- FullCalendar -->
    <FullCalendar :options="calendarOptions" ref="calendar" />
    
    <!--  -->
    <div v-if="authStore.isAuthenticated" class="mt-6 bg-white p-6 rounded-lg shadow-md max-w-md">
      <h2 class="text-xl font-semibold mb-4">{{ formMode === 'add' ? '' : '' }}</h2>
      <form @submit.prevent="handleSubmit">
        <div class="mb-4">
          <label for="title" class="block text-sm font-medium text-gray-700"></label>
          <input
            type="text"
            id="title"
            v-model="form.title"
            class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
            required
          />
        </div>
        <div class="mb-4">
          <label for="room" class="block text-sm font-medium text-gray-700"></label>
          <select
            id="room"
            v-model="form.room"
            class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
            required
          >
            <option v-for="room in bookingStore.rooms" :key="room" :value="room">
               {{ room }}
            </option>
          </select>
        </div>
        <div class="mb-4">
          <label for="start" class="block text-sm font-medium text-gray-700"></label>
          <input
            type="datetime-local"
            id="start"
            v-model="form.start"
            class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
            required
          />
        </div>
        <div class="mb-4">
          <label for="end" class="block text-sm font-medium text-gray-700"></label>
          <input
            type="datetime-local"
            id="end"
            v-model="form.end"
            class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
            required
          />
        </div>
        <div class="flex gap-2">
          <button
            type="submit"
            class="bg-blue-600 text-white p-2 rounded-md hover:bg-blue-700"
          >
            {{ formMode === 'add' ? '' : '' }}
          </button>
          <button
            v-if="formMode === 'edit'"
            @click="resetForm"
            type="button"
            class="bg-gray-500 text-white p-2 rounded-md hover:bg-gray-600"
          >
            
          </button>
          <button
            v-if="formMode === 'edit'"
            @click="deleteBooking"
            type="button"
            class="bg-red-600 text-white p-2 rounded-md hover:bg-red-700"
          >
            
          </button>
        </div>
        <p v-if="error" class="mt-4 text-red-600">{{ error }}</p>
      </form>

      <!-- / -->
      <div class="mt-6">
        <h3 class="text-lg font-semibold mb-2"></h3>
        <div class="flex gap-2">
          <button
            @click="downloadBookings"
            class="bg-blue-500 text-white p-2 rounded-md hover:bg-blue-600"
          >
            
          </button>
          <label class="bg-yellow-500 text-white p-2 rounded-md hover:bg-yellow-600 cursor-pointer">
            
            <input
              type="file"
              accept=".json"
              @change="uploadBookings"
              class="hidden"
            />
          </label>
        </div>
        <p v-if="uploadError" class="mt-2 text-red-600">{{ uploadError }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useAuthStore } from '@/stores/auth'
import { useBookingStore } from '@/stores/bookings'
import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'

export default {
  name: 'CalendarView',
  components: { FullCalendar },
  setup() {
    const authStore = useAuthStore()
    const bookingStore = useBookingStore()
    const roomFilter = ref('')
    const calendar = ref(null)
    const showImage = ref(false)
    const uploadError = ref('')

    // 
    bookingStore.fetchBookings()

    // 
    const filteredEvents = computed(() => {
      if (!roomFilter.value) return bookingStore.bookings
      return bookingStore.bookings.filter(b => b.room === roomFilter.value)
    })

    // 
    const calendarOptions = ref({
      plugins: [dayGridPlugin, timeGridPlugin, interactionPlugin],
      initialView: 'dayGridMonth',
      headerToolbar: {
        left: 'prev,next today',
        center: 'title',
        right: 'dayGridMonth,timeGridWeek,timeGridDay'
      },
      events: filteredEvents,
      editable: authStore.isAuthenticated,
      selectable: authStore.isAuthenticated,
      select: handleSelect,
      eventClick: handleEventClick
    })

    // 
    const form = ref({
      id: null,
      title: '',
      room: bookingStore.rooms[0] || 'A',
      start: '',
      end: ''
    })
    const formMode = ref('add')
    const error = ref('')

    // 
    function handleSelect(selectInfo) {
      if (!authStore.isAuthenticated) return
      const start = new Date(selectInfo.startStr).toISOString().slice(0, 16)
      const end = new Date(selectInfo.endStr).toISOString().slice(0, 16)
      form.value = {
        id: null,
        title: '',
        room: bookingStore.rooms[0] || 'A',
        start,
        end
      }
      formMode.value = 'add'
      error.value = ''
    }

    // 
    function handleEventClick(clickInfo) {
      if (!authStore.isAuthenticated) return
      form.value = {
        id: clickInfo.event.id,
        title: clickInfo.event.title.replace(`${clickInfo.event.extendedProps.room} - `, ''),
        room: clickInfo.event.extendedProps.room,
        start: clickInfo.event.start.toISOString().slice(0, 16),
        end: clickInfo.event.end.toISOString().slice(0, 16)
      }
      formMode.value = 'edit'
      error.value = ''
    }

    // 
    function handleSubmit() {
      if (!form.value.title || !form.value.room || !form.value.start || !form.value.end) {
        error.value = ''
        return
      }
      const startDate = new Date(form.value.start)
      const endDate = new Date(form.value.end)
      if (isNaN(startDate) || isNaN(endDate)) {
        error.value = ''
        return
      }
      if (endDate <= startDate) {
        error.value = ''
        return
      }
      const booking = {
        title: `${form.value.room} - ${form.value.title}`,
        room: form.value.room,
        start: startDate.toISOString(),
        end: endDate.toISOString()
      }
      try {
        if (formMode.value === 'add') {
          console.log('Submitting new booking:', booking)
          bookingStore.addBooking(booking)
        } else {
          console.log('Updating booking:', booking)
          bookingStore.updateBooking(form.value.id, booking)
        }
        resetForm()
        if (calendar.value) {
          calendar.value.getApi().render()
        }
      } catch (err) {
        console.error('Submit error:', err)
        error.value = ''
      }
    }

    // 
    function deleteBooking() {
      if (confirm('')) {
        try {
          console.log('Deleting booking:', form.value.id)
          bookingStore.deleteBooking(form.value.id)
          resetForm()
          if (calendar.value) {
            calendar.value.getApi().render()
          }
        } catch (err) {
          console.error('Delete error:', err)
          error.value = ''
        }
      }
    }

    // 
    function downloadBookings() {
      const data = {
        bookings: bookingStore.bookings,
        rooms: bookingStore.rooms
      }
      const blob = new Blob([JSON.stringify(data, null, 2)], { type: 'application/json' })
      const url = URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = 'bookings.json'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      URL.revokeObjectURL(url)
    }

    // 
    function uploadBookings(event) {
      const file = event.target.files[0]
      if (!file) {
        uploadError.value = ''
        return
      }
      if (!file.name.endsWith('.json')) {
        uploadError.value = ' JSON '
        return
      }
      const reader = new FileReader()
      reader.onload = (e) => {
        try {
          const data = JSON.parse(e.target.result)
          if (!Array.isArray(data.bookings) || !Array.isArray(data.rooms)) {
            throw new Error('')
          }
          //  bookings rooms
          bookingStore.bookings = data.bookings.filter(b => bookingStore.rooms.includes(b.room))
          bookingStore.saveBookings()
          uploadError.value = ''
          if (calendar.value) {
            calendar.value.getApi().render()
          }
        } catch (err) {
          console.error('Upload error:', err)
          uploadError.value = ''
        }
      }
      reader.onerror = () => {
        uploadError.value = ''
      }
      reader.readAsText(file)
      event.target.value = '' //  input
    }

    // 
    function resetForm() {
      form.value = {
        id: null,
        title: '',
        room: bookingStore.rooms[0] || 'A',
        start: '',
        end: ''
      }
      formMode.value = 'add'
      error.value = ''
    }

    // 
    function applyFilter() {
      if (calendar.value) {
        calendar.value.getApi().render()
      }
    }

    return {
      authStore,
      bookingStore,
      calendarOptions,
      calendar,
      form,
      formMode,
      error,
      roomFilter,
      showImage,
      uploadError,
      handleSubmit,
      deleteBooking,
      resetForm,
      applyFilter,
      downloadBookings,
      uploadBookings
    }
  }
}
</script>

<style scoped>
.room-image-preview {
  display: none;
  position: absolute;
  z-index: 10;
  background: white;
  border: 1px solid #ccc;
  padding: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  max-width: 500px; /*  */
}

.room-info-link:hover + .room-image-preview,
.room-image-preview:hover {
  display: block;
}

img {
  width: 100%; /*  */
  height: auto; /*  */
  max-height: 80vh; /*  */
  object-fit: contain; /*  */
}
</style>